/*********** HR style 1 *********/
hr {
  position: relative;
  display: block;
  overflow: visible;
  margin: 1em 0;
  padding: 0;
  height: 1px;
  border: 0;
  border-top: 1px #eeeded solid;
}
hr:before {
  position: absolute;
  top: -1px;
  left: 0;
  display: inline-block;
  width: 20%;
  height: 1px;
  content: "";
}
hr:after {
  position: absolute;
  top: -1px;
  right: 0;
  display: inline-block;
  width: 20%;
  height: 1px;
  content: "";
}
/*********** HR style 2 *********/
.hr-style2:before {
  left: 30%;
  width: 40%;
}
.hr-style2:after {
  display: none;
}
/*********** HR style 3 *********/
.hr-style3:before {
  left: 20%;
  width: 20%;
}
.hr-style3:after {
  right: 20%;
  width: 20%;
}
/*********** HR style 4 *********/
.hr-style4:before {
  left: 44%;
  margin-top: 0;
  width: 5%;
  content: "\f105";
  text-align: right;
  font-size: 20px;
  font-family: FontAwesome;
  line-height: 1px;
}
.hr-style4:after {
  right: 44%;
  margin-top: 0 ;
  width: 5%;
  content: "\f104";
  text-align: left;
  font-size: 20px;
  font-family: FontAwesome;
  line-height: 1px;
}
/*********** HR style 5 *********/
.hr-style5:before {
  left: 44%;
  margin-top: 0 ;
  width: 5%;
  content: "\f1d8";
  text-align: center;
  font-size: 20px;
  font-family: FontAwesome;
  line-height: 1px;
}
.hr-style5:after {
  right: 44%;
  margin-top: 0 ;
  width: 5%;
  content: "\f1d8";
  text-align: center;
  font-size: 20px;
  font-family: FontAwesome;
  line-height: 1px;
  -moz-transform: rotateY(180deg);
  -ms-transform: rotateY(180deg);
  -o-transform: rotateY(180deg);
  -webkit-transform: rotateY(180deg);
  transform: rotateY(180deg);
}
.hr-style5:before,
.hr-style5:after {
  background: #fff;
  color: #d4d4d4;
}
