.footer .NL:before {
  position: absolute;
  top: 16px;
  left: 24px;
  display: inline-block;
  color: #5C5C5C;
  content: "\f0e0";
  font-family: FontAwesome;
}
.footer .NL .NL-btn {
  background: #3F3F3F;
  color: #fff;
}
.footer .NL .NL-btn:hover {
  background: #ffd400;
}
.footer .no-margin {
  margin: 20px 20px 8px;
  text-align: center;
  font-size: 12px;
}
/******* footer Latest tweets *******/
.footer-top .tweet {
  position: relative;
  padding: 0 0 5px 45px;
  font-size: 12px;
  min-height: 35px;
}
.footer-top .tweet a:hover {
  color: #e7512f;
}
.footer-top .tweet .fa-twitter {
  position: absolute;
  top: 3px;
  left: 0;
  color: #5c5c5c;
  font-size: 250%;
}
.footer-top .tweet p {
  margin: 0 0 10px;
  font-size: 12px;
}
/******* footer tags *******/
.footer-top .tags a {
  display: block;
  float: left;
  margin: 3px;
  padding: 8px 6px;
  background: #2C2C2C;
  color: #C4C4C4;
  font-size: 11px;
  text-transform: capitalize;
}
/******* footer tags *******/
.footer-contact {
  padding: 7px 0;
      color: #9E9E9E;

}
.footer-contact i {
  display: table-cell;
  padding: 0 12px 0 0;
  color: #6a6a6a;
  vertical-align: middle;
  font-size: 18px;
}
.footer-contact span {
  display: table-cell;
}
/******* Footer Flickr **********/
.footer-top .flickr-stream-w ul li img,
.footer-top .flickr-stream-w .img-overlay {
  width: 61px;
  height: 61px;
}
.footer-top .flickr-stream-w .img-overlay:before {
  font-size: 22px;
  line-height: 61px;
}
.footer-top .flickr {
  height: 61px;
  display: block;
  overflow: hidden;
}
/******* copyrights *******/
.copyrights {
  padding-top: 10px;
  padding-bottom: 10px;
  /*white-space: nowrap;*/
  word-wrap:break-word;
  font-size: 11px;
  color: #9E9E9E;
}
.copyrights a {
  color: #7C7C7C;
  text-decoration: underline;
}
.copyrights a:hover {
  text-decoration: none;
  color: #e7512f;
}
.footer-bottom {
  overflow: auto;
  padding: 15px 0;
  border-top: 1px #363636 solid;
  background: #1D1D1D;
}
/******* footer social list *******/
.social-list li {
  float: left;
  margin: 0 1px 0 0;
  font-size: 18px;
}
.social-list li a {
  display: block;
  padding: 0;
  color: #737373;
}
.social-list li span {
  display: block;
  padding: 0 12px;
  text-align: center;
  line-height: 33px;
}
.social-list li a:hover {
  text-decoration: none;
}
.top-bar .social-list li span {
  line-height: 39px;
}
